<template>
	<div class="all">
		<!-- 子组件 -->
		<topic-list :topics="topics"></topic-list>
		<!--加载组件-->
		<infinite-loading @infinite="infiniteHandler"></infinite-loading>
	</div>
</template>

<script>
	// 引入请求数据的模块
	import { getTopicList } from "@/getdata/index.js";
	// 引入主题 列表 组件
	import TopicList from "@/components/topics/TopicList";
	// 加载 上下滑动模块
	import InfiniteLoading from 'vue-infinite-loading';
	
	export default {
		name: 'All',
		// 数据
		data() {
			return {
				topics: [],
				//页面
      			page:1,
      			//控制页面滑动到底部,如果有数据就显示loading,如果没有就不显示
      			loadingIsShow:true
			}
		},
		// 加载组件
		components: {
			TopicList,
			InfiniteLoading
		},
		 // 钩子函数
		mounted(){
			// 调用查询数据的函数
		    getTopicList({limit:20,page:this.page}).then((data)=>{
		    	this.topics = data.data.data
		    })
		},
		methods:{
		    infiniteHandler($state) {
		    	setTimeout(() => {
		        	getTopicList({limit:10,page:++(this.page)}).then(data=>{
		        	// console.log(data);
		        	this.topics = this.topics.concat(data.data.data);
		        	// 取数据的时候判断data.data.data的length  
			        if(data.data.data.length==0){
			            this.loadingIsShow = false;
			        }else{
			            // 重新请求数据
			            $state.loaded();  
			        }
		        })
		        // 重新请求数据
		        $state.loaded();  
		      }, 1000);
		    }
		},
	}
</script>
<style lang="scss">
/*加载基础的样式*/
@import "../../assets/sass/base.scss";
.all{
  margin-bottom: rem(100px);
}
</style>


