<template>
	<div class="me">
		<div class="about">
			<div class="top">
				<span class="span_one">主页</span><span class="">/　关于</span>
			</div>
			<div class="bottom">
				<h1>关于</h1>
				<p style="margin-top: 36px;">Vue 中文社区是全球最大的 Vueframework 中文开源技术社区，致力于 Vue 在中国的学习、推广、研究工作。</p>
				<p style="margin-top: 36px;">Vue 中文社区正在努力建设中，如果你有什么想法，欢迎
					<a style="color: #778087;" href="http://www.vue-js.com/?tab=bb" target="_blank">吐槽</a>。</p>
				<img src="http://pub.idqqimg.com/wpa/images/group.png" title="vueJs社区交流讨论2">
				<p class="erweima">群二维码</p>
				<p>尽管建了QQ群，但是还是希望大家尽量有问题在社区讨论，这样才可以有一些沉淀和积累，不至于每天都有人在问同样的问题，通过对问题的总结和用文字表达的过程，自己也可以对知识进行一个梳理。</p>
				<h1 class="yidong">移动客户端</h1>
				<p style="margin-top: 36px;">客户端目前正在加急开发中…静请期待</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Me',
		data() {
			return {
				msg: 'Welcome to Your Vue.js App'
			}
		}
	}
</script>
<style scoped lang="scss">
	@import '../assets/sass/base.scss';
	.me {
		width: 98%;
		height: 1333px;
		padding: rem(8px);
		background: #f5f5f5;
		.about {
			width: 98%;
			margin: 0 auto;
			background: #FFF;
			border-radius: 10px;
			.top {
				width: 100%;
				height: rem(80px);
				background: #f5f5f5;
				border-radius: rem(10px) rem(10px) 0 0;
				span {
					font-size: rem(30px);
					line-height: rem(66px);
					color: #999;
				}
				.span_one {
					color: #369219;
				}
			}
			.bottom {
				padding: rem(30px);
				h1 {
					line-height: 130px;
					border-bottom: 1px solid #999;
				}
				p {
					color: #333;
					font-size: rem(28px);
					line-height: rem(44px);
				}
				img {
					height: rem(40px);
					margin: rem(36px) 0;
				}
				.erweima {
					margin-bottom: rem(26px);
				}
				.yidong {
					margin-top: rem(50px);
				}
			}
		}
	}
</style>


