<template>
	<div class="topic">
		<div class="neirong">
			<div class="title">{{topicData.title}}</div>
			<p class="jieshao">● 发布于一年前 ● 作者　bailicangdu ● 130869 次浏览 ● 来自 分享</p>
			<hr />
			<div class="tuijian">
				<div class="left"></div>
				<div class="right">
					社区推荐：<span style="color:#0088cc">掘金</span>是一个面向程序员的的技术社区，从 Vue 到 React，从前端教程到开源库，无论是入门还是进阶，来<span style="color:#0088cc">掘金</span>你不会错过前端开发的任何一个技术干货。
				</div>
			</div>
			<div class="content" v-html="topicData.content"></div>
		</div>
	</div>
</template>

<script>
	// 引入请求数据的模块
	import { getTopicShow } from "@/getdata/index.js"

	export default {
		name: 'HelloWorld',
		data() {
			return {
				topicData: []
			}
		},
		mounted() {
			getTopicShow(this.$route.params.id).then((data) => {
				this.topicData = data.data.data;
				console.log(data)
			})
		}
	}
</script>
<style lang="scss">
	// 加载基础的样式
	@import "../../assets/sass/base.scss";
	.topic {
		margin-top: rem(80px);
		margin-bottom: rem(100px);
		padding: 0 rem(10px);
		background: #f6f6f6;
		.neirong {
			background: #FFF;
			border-radius: rem(30px);
			padding: 0 rem(30px);
			.title {
				overflow: hidden;
				text-align: center;
				font-size: rem(55px);
				font-weight: bold;
				line-height: rem(100px);
				color: #333;
			}
			.jieshao {
				color: #838383;
				font-size: rem(10px);
			}
			.content {
				overflow: hidden;
				max-width: rem(750px);
				h1 {
					font-size: rem(64px);
					border-bottom: 1px solid #838383;
				}
				p {
					margin-top: rem(30px);
					font-size: rem(24px);
					line-height: rem(50px);
					color: #333;
				}
			}
			hr {
				margin: rem(30px) 0;
			}
			.tuijian {
				width: 100%;
				margin: 0 auto rem(20px);
				.right {
					padding: rem(20px);
					background: #f6f6f6;
					border-left: rem(7px) solid #099436;
					font-size: rem(26px);
					color: #778087;
					line-height: rem(50px);
				}
			}
		}
	}
</style>


