<template>
	<div class="index">

		<nav class="top">
			<!-- Swiper -->
			<div class="swiper-container">
				<div class="swiper-wrapper">
					<div class="swiper-slide">
						<router-link to="/index/all" tag="a" :class=" respath=='all'?'bgt':''">全部</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="/index/good" tag="a" :class=" respath=='good'?'bgt':''">精华</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="/index/weex" tag="a" :class=" respath=='weex'?'bgt':''">weex</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="/index/share" tag="a" :class=" respath=='share'?'bgt':''">分享</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="/index/ask" tag="a" :class=" respath=='ask'?'bgt':''">问答</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="/index/job" tag="a" :class=" respath=='job'?'bgt':''">招聘</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="" tag="a">咳咳</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="" tag="a">啦啦</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="" tag="a">呵呵</router-link>
					</div>
					<div class="swiper-slide">
						<router-link to="" tag="a">哈哈</router-link>
					</div>
				</div>
			</div>
		</nav>

		<!--<h1 style="margin-top: 100px;">{{respath}}</h1>-->
		<!--放子组件的视图-->
		<router-view/>

	</div>
</template>

<script>
	export default {
		name: 'Index',
		// 计算属性
		computed: {
			// 计算路径
			respath: function() {
				// 把路径分割成数组
				var pathArr = this.$route.path.split('/');
				//				console.log(pathArr)
				// 返回表示一级路由的str
				return pathArr[2];
			}
		},
		// 钩子函数
		mounted() {
			//自己的js就写在这里面
			var swiper = new Swiper('.swiper-container', {
				slidesPerView: 6,
				spaceBetween: 20,
			});

		},

	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	/*加载基础的样式*/
	
	@import '../assets/sass/base.scss';
	/*导航*/
	
	html,
	body {
		background: green;
	}
	
	nav.top {
		width: 100%;
		height: rem(77px);
		position: fixed;
		left: 0;
		top: 0;
		background: #F6F6F6;
		display: flex;
		align-items: center;
		/*swiper的导航组件*/
		.swiper-container {
			.swiper-slide {
				font-size: rem(28px);
				text-align: center;
				a {
					line-height: rem(77px);
					color: #369219;
					padding: rem(5px) rem(10px);
					border-radius: rem(5px);
				}
				.bgt {
					background: #369219;
					color: #fff;
					border-radius: rem(7px);
				}
			}
		}
	}
</style>


